import './App.css';
import React from "react";
import Navbar from "./components/navbar";
import Heading from "./components/Heading";
import Duvody from "./components/duvody";
import  {BrowserRouter as Router, Switch, Route} from "react-router-dom";
import Aktuality from "./components/Aktuality";
import Akce from "./components/Akce";
import Footer from "./components/footer";



function App() {
    window.onscroll = function(e) {

        if(window.innerWidth > 960) {
            document.getElementById("duvod1").style.marginTop  = 20 + -this.scrollY * 0.01 + "%";

            document.getElementById("header").style.top = 0.2 * -this.scrollY + "px";
            document.getElementById("header").style.opacity =  100-0.5*this.scrollY + "%";
            document.getElementById("titleImage").style.top = 0.05 * -this.scrollY + "px";
            document.getElementById("titleImage").style.opacity =  200-0.3*this.scrollY + "%";
        }
        else
        {
            document.getElementById("header").style.top = 0.4 * -this.scrollY + "px";
            document.getElementById("header").style.opacity =  150-this.scrollY + "%";
            document.getElementById("titleImage").style.top = 0.05 * -this.scrollY + "px";
            document.getElementById("titleImage").style.opacity =  400-this.scrollY + "%";
        }

    }
  return (
      <>

          <Router>
              <Navbar />
              <Heading />
              <Duvody />
              <Aktuality/>
              <Akce/>
              <Footer/>
          </Router>

  </>
      );
}

export default App;
