import React, {useState} from "react";
import {Link} from "react-router-dom";
import "./Akce.css";


function Akce() {



    var xPos = -300;
    const moveRight = () => {
        if(xPos>-300)
            xPos -= 150;
        document.getElementById("akc1").style.transform = "translate(" + xPos + "%,0%)";
        document.getElementById("akc2").style.transform = "translate(" + xPos + "%,0%)";
        document.getElementById("akc3").style.transform = "translate(" + xPos + "%,0%)";
        if(xPos == -300)
            document.getElementById("rightArrowAkce").style.display = "none";
        document.getElementById("leftArrowAkce").style.display = "unset";
    };
    const moveLeft = () => {
        if(xPos<0)
            xPos += 150;
        document.getElementById("akc1").style.transform = "translate(" + xPos + "%,0%)";
        document.getElementById("akc2").style.transform = "translate(" + xPos + "%,0%)";
        document.getElementById("akc3").style.transform = "translate(" + xPos + "%,0%)";
        if(xPos == 0)
            document.getElementById("leftArrowAkce").style.display = "none";
        document.getElementById("rightArrowAkce").style.display = "unset";

    };

    return(
        <>
            <div className="panelAkce">
                <p className="panel_nadpisAkce">Akce</p> <hr className="whiteLineAkce"/>
                <img  className="leftArrowAkce" id = "leftArrowAkce" src={'/images/arrow3.png'} onClick={moveLeft} />
                <img className="rightArrowAkce" id = "rightArrowAkce" src={'/images/arrow3.png'} onClick={moveRight}/>
                <div className="akceContainer">

                    <div className="akce" id = "akc1"><p className="nazevAkce">Lorem ipsum</p>
                        <p className="textAkce">Lorem ipsum dolor sit amet,<br/> consectetur adipiscing elit,
                            sed do eiusmod <br/>tempor incididunt ut labore et dolore <br/> magna aliqua.Ut enim ad minim<br/>
                            veniam, quis nostrud exercitation ullamco<br/> </p></div>

                    <div className="akce" id = "akc2"><p className="nazevAkce">Lorem ipsum</p>
                        <p className="textAkce">Lorem ipsum dolor sit amet,<br/> consectetur adipiscing elit,
                            sed do eiusmod <br/>tempor incididunt ut labore et dolore <br/> magna aliqua.Ut enim ad minim<br/>
                            veniam, quis nostrud exercitation ullamco<br/> </p></div>

                    <div className="akce" id = "akc3"><p className="nazevAkce">Lorem ipsum</p>
                        <p className="textAkce">Lorem ipsum dolor sit amet,<br/> consectetur adipiscing elit,
                            sed do eiusmod <br/>tempor incididunt ut labore et dolore <br/> magna aliqua.Ut enim ad minim<br/>
                            veniam, quis nostrud exercitation ullamco<br/> </p></div>
                    <div className="akceBtn"><p>Více akcí</p></div>
                    <img  id  = "siluetaAkce" src={'/images/silueta2.svg'} />
                </div>

            </div>


        </>
    );
}
export default Akce;