import React, {useState} from "react";
import {Link} from "react-router-dom";
import "./Aktuality.css";


function Aktuality() {



    var xPos = 0;
    const moveRight = () => {
        if(xPos>-300)
            xPos -= 150;
        document.getElementById("akt1").style.transform = "translate(" + xPos + "%,0%)";
        document.getElementById("akt2").style.transform = "translate(" + xPos + "%,0%)";
        document.getElementById("akt3").style.transform = "translate(" + xPos + "%,0%)";
        if(xPos == -300)
            document.getElementById("rightArrow").style.display = "none";
        document.getElementById("leftArrow").style.display = "unset";
    };
    const moveLeft = () => {
        if(xPos<0)
            xPos += 150;
        document.getElementById("akt1").style.transform = "translate(" + xPos + "%,0%)";
        document.getElementById("akt2").style.transform = "translate(" + xPos + "%,0%)";
        document.getElementById("akt3").style.transform = "translate(" + xPos + "%,0%)";
        if(xPos == 0)
            document.getElementById("leftArrow").style.display = "none";
        document.getElementById("rightArrow").style.display = "unset";

    };

    return(
        <>
            <div className="panel">
                <p className="panel_nadpis">Aktuality</p> <hr className="whiteLine"/>
                <img  className="leftArrow" id = "leftArrow" src={'/images/arrow3.png'} onClick={moveLeft} />
                <img className="rightArrow" id = "rightArrow" src={'/images/arrow3.png'} onClick={moveRight}/>
                <div className="aktualityContainer">

                    <div className="aktualita" id = "akt1"><p className="nazevAktuality">Lorem ipsum</p>
                        <p className="textAktuality">Lorem ipsum dolor sit amet,<br/> consectetur adipiscing elit,
                            sed do eiusmod <br/>tempor incididunt ut labore et dolore <br/> magna aliqua.Ut enim ad minim<br/>
                            veniam, quis nostrud exercitation ullamco<br/> </p></div>

                    <div className="aktualita" id = "akt2"><p className="nazevAktuality">Lorem ipsum</p>
                        <p className="textAktuality">Lorem ipsum dolor sit amet,<br/> consectetur adipiscing elit,
                            sed do eiusmod <br/>tempor incididunt ut labore et dolore <br/> magna aliqua.Ut enim ad minim<br/>
                            veniam, quis nostrud exercitation ullamco<br/> </p></div>

                    <div className="aktualita" id = "akt3"><p className="nazevAktuality">Lorem ipsum</p>
                        <p className="textAktuality">Lorem ipsum dolor sit amet,<br/> consectetur adipiscing elit,
                            sed do eiusmod <br/>tempor incididunt ut labore et dolore <br/> magna aliqua.Ut enim ad minim<br/>
                            veniam, quis nostrud exercitation ullamco<br/> </p></div>
                    <div className="aktualityBtn"><p>Více aktualit</p></div>
                </div>

            </div>


        </>
    );
}
export default Aktuality;